import React, { Component } from 'react';
import {Form, Ul, Div1, Div2, Span1, DivThanks} from './ContactForm.style';

export class ContactForm extends Component {
	constructor(props){
		super(props);
		this.state = {
			pointerEvents: '',
			color: '',
			backColor: '',
			border: '',
			boxS: '',
		};
	}

	setPointer(){
		this.setState({
			pointerEvents: 'none',
			color: '#666666',
			backColor: '#cccccc',
			border: '1px solid #999999',
			boxS: 'none'
		}); 
		setTimeout(function(){
			this.setState({
				pointerEvents: '',
				color: '',
				backColor: '',
				border: '',
				boxS: '',
			});
		}.bind(this),3000);
	}

	render() {
		return (

			<div className="container-fluid">
				<div className="row">
					<div style={{backgroundColor: 'blue', height: '300px'}} className="col-xs-12 col-sm-12 col-md-6">
					</div>
					<div style={{backgroundColor: 'red', height: '300px'}} className="col-xs-12 col-sm-12 col-md-6">
					</div>
					{/*<Div1 data-aos="slide-right" data-aos-anchor-placement="top-bottom" className="col-xs-12 col-sm-12 col-md-5">
						<div style={{paddingTop: '13%', paddingBottom: '13%'}}>
							<h1>Contctame</h1>
							<h3>QU NECESITAS?</h3>
							<p>
								Si estas interesado en mi trabajo o simplemente necesitas
								tu pgina web, no dudes en contactarme completando el
								siguiente formulario.
							</p>
							<Span1 id="span1" className="glyphicon glyphicon-map-marker"></Span1>
							<h4 style={{display: 'inline-block'}}>Caracas, Venezuela</h4>
						</div>
					</Div1>
					<Div2 style={{paddingTop: '4%', paddingLeft: '15%', paddingRight: '15%', paddingBottom: '4%'}} className="col-xs-12 col-sm-12 col-md-7">
						<Form data-aos="fade-in" id="gform" method="POST" action="https://script.google.com/macros/s/AKfycbyxUIvyz_70gCvixgbKix-ZyRtCfIV4rSizDMeSEGKknaw8et60/exec">
							<Ul>
							    <li>
							        <input id="name" type="text" name="Nombre" placeholder="Nombre*" required/>
							    </li>
							    <li>
							        <input id="email" type="email" name="email" placeholder="Email*" required/>
							        <span id="email-invalid" style={{display: 'none'}}>Must be a valid email address</span>
							    </li>
							    <li>
							        <textarea id="message" name="Mensaje" placeholder="Mensaje*" cols="40" rows="6" required/>
							    </li>
							    <li>
							        <button onClick={this.setPointer.bind(this)} style={{pointerEvents: this.state.pointerEvents, color: this.state.color, backgroundColor: this.state.backColor, border: this.state.border, boxShadow: this.state.boxS}} className="sendbtn">Enviar</button>
							    </li>
							</Ul>
						</Form>
						<DivThanks id="thankyou_message">
						    <h2>Gracias por escribir!</h2>
						    <h3>Muy pronto me pondr en contacto contigo</h3>
						</DivThanks>
					</Div2>*/}
				</div>
			</div>
		);
	}
}



